import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import axios from "axios";
import router from "../router";
export default new Vuex.Store({
  state: {
    baseUrl:"https://api-kasb.maktab.uz/api/v1/",
    headers:[],
    banners:[],
    services:[],
    universities:[],
    universitiestop:[],
    newssingle:[],
    news:[],
    tests:[],
      testsId:[],
    universitiessingle:{},
      professions_web:[],
      professions_websingle:[],
      subjects:[],
      subjectswork:[],
      isLoading:false,
      refCount:0,
      me:false,
      isVideo:false,
      bannersI:[],
      test_one:[]
  },
    mutations: {

        loading(state, isLoading) {
            console.log({isLoading})
            if (isLoading) {
                state.refCount++;
                state.isLoading = true;
            } else if (state.refCount > 0) {
                state.refCount--;
                state.isLoading = (state.refCount > 0);
            }
        },
    },
  actions: {
      getMe({state}){
          axios.get(`${state.baseUrl    }students/me/`,
              {
                  headers: {
                      'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                      'Accept-Language':'oz'
                  }
              }
              )
              .then(res=>{
                  state.me=res.data
              })


      },
    getMenus({state}){
          axios.get(`${state.baseUrl}header/`,{
            headers:{
                'Accept-Language':localStorage.getItem('lang')
            }
          })
          .then(res=>{
            console.log(res.data.menus)
              state.headers=res.data
          })

    },
    getBanners({state}){
      axios.get(`${state.baseUrl}banners/`,{
        headers:{
            'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.banners=res.data

          })
    },
      getBannersIndex({state}){
          axios.get(`${state.baseUrl}index-banner/`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.bannersI=res.data

              })
      },
    getServices({state}){
      axios.get(`${state.baseUrl}services/`,{
        headers:{
            'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.services=res.data

          })
    },
      getUniversityTop({state}){
          axios.get(`${state.baseUrl}top-universities`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.universitiestop=res.data
              })
      },
      getUniversity({state},query){
      axios.get(`${state.baseUrl}universities/?${query.type}&&page=${query.page}`,{
        headers:{
            'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.universities=res.data
          })
    },
    getUniversitySingle({state}){
      console.log(router.currentRoute)
      axios.get(`${state.baseUrl}universities/${router.currentRoute.params.id}/`,{
        headers:{
            'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.universitiessingle=res.data
            console.log(router)
          })
    },
      getNewssitySingle({state}){
          console.log(router.currentRoute)
          axios.get(`${state.baseUrl}news/${router.currentRoute.params.id}/`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.newssingle=res.data
                  console.log(router)
              })
      },
    getNews({state}){
      axios.get(`${state.baseUrl}top-web-news/`,{
        headers:{
            'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            state.news=res.data
          })
    },
      getSNews({state}){
          axios.get(`${state.baseUrl}admin/news/${router.currentRoute.params.id}`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.news=res.data
              })
      },
      getTests({state}){
          axios.get(`${state.baseUrl}tests/`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.tests=res.data
              })
      },
      getTestsId({state},cre){
          axios.get(`${state.baseUrl}tests/${cre}`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.testsId=res.data
              })
      },
      getProfision({state},page){
          axios.get(`${state.baseUrl}professions-web/?page=${page}`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.professions_web=res.data
              })
      },
      getProfisionFilt({state},page){
          axios.get(`${state.baseUrl}professions-web/?${page}`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.professions_web=res.data
              })
      },
      getProfisionSingle({state}){
          console.log(router.currentRoute)
          axios.get(`${state.baseUrl}professions-web/${router.currentRoute.params.id}/`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.professions_websingle=res.data
                  console.log(router)
              })
      },
      getSubjects({state}){
          console.log(router.currentRoute)
          axios.get(`${state.baseUrl}subjects/`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.subjects=res.data
                  console.log(router)
              })
      },
      getSubjectWork({state},cre){
          axios.get(`${state.baseUrl}professions-web-calculator?${cre}`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.professions_web=res.data
              })
      },
      getQuizOne({state},cre){
          axios.get(`${state.baseUrl}/tests/${cre}/questions/`,{
              headers:{
                  'Accept-Language':localStorage.getItem('lang')
              }
          })
              .then(res=>{
                  state.test_one=res.data
                  for (var i=0;i<state.test_one.length;i++){
                      for (var j=0;j<state.test_one[i].choices.length;j++){
                          state.test_one[i].choices[j].active=false
                      }

                  }
              })
      },
  },
  modules: {
  }
})
